                    for (var i = 0; i < data.mgrList.length; i++) {
                        this.newDirectorLisr.push({ "label": data.mgrList[i].assitMgrName, "value": data.mgrList[i].assitMgr });//主管列表
                    }

                    // this.msgs = [];
