    //添加分成协储员信息 
    newCooperatorPreservation() {
        if (this.queryBean.newAssitId == null || this.queryBean.newAssitId == undefined || this.queryBean.newAssitId == "") {
            this.newCooperatorDispaly = true;
        } else {
            this.newCooperatorDispaly = false;
        }
        if (this.queryBean.newDividerate == null || this.queryBean.newDividerate == undefined || this.queryBean.newDividerate == "") {
            this.newProportionDispaly = true;
        } else {
            this.newProportionDispaly = false;
        }
        if (this.queryBean.newAssitMgr == null || this.queryBean.newAssitMgr == undefined || this.queryBean.newAssitMgr == "") {
            this.newDirectorDispaly = true;
        } else {
            this.newDirectorDispaly = false;
        }

        // 校验必输项
        if (this.newCooperatorDispaly || this.newProportionDispaly || this.newDirectorDispaly) {
            return
        }

        // 校验分成比例
        var regAmt = /^[1-9]{0,1}[0-9]{0,1}(\.[0-9]{0,2})?$/;
        var regAmtMax = /^[1]{1}[0]{1}[0]{1}(\.[0]{0,5})?$/;
        if (!regAmtMax.test(this.queryBean.newDividerate)) {
            if (!regAmt.test(this.queryBean.newDividerate)) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: "分成比例数值在（0，100】之间" }];
                return
            } else if (parseFloat(this.queryBean.newDividerate) == 0) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: "分成比例数值在（0，100】之间" }];
                return
            } else {
                // 金额存在.，.前面和后面必须有值
                if (this.queryBean.newDividerate.indexOf('.') != -1) {
                    var arr = [];
                    arr = this.queryBean.newDividerate.split('.');
                    if (arr[0] == '' || arr[1] == '') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: "分成比例数值在（0，100】之间" }];
                        return
                    }
                }
            }
        }
        //满足全部必输 才进入方法
        if (this.newCooperatorDispaly == false && this.newProportionDispaly == false && this.newDirectorDispaly == false) {
            let bool = true;//判断是否数据重复导入
            for (var i = 0; i < this.newCooperatorTableValueList.length; i++) {
                if (this.newCooperatorTableValueList[i].assitId == this.queryBean.newAssitId) {
                    bool = false;
                    break;
                }
            }
            if (bool) {
                for (var i = 0; i < this.newDirectorLisr.length; i++) {
                    if (this.newDirectorLisr[i].value == this.queryBean.newAssitMgr) {
                        this.queryBean.newAssitMgrName = this.newDirectorLisr[i].label;//为了获取协储员主管名称
                    }
                }
                this.newCooperatorTableValueListAdd = [];
                this.newCooperatorTableValueListAdd.push({
                    "assitId": this.queryBean.newAssitId,
                    "assitName": this.queryBean.newAssitName,
                    "assitBranch": this.queryBean.newAssitBranch,
                    "assitBranchName": this.queryBean.newAssitBranchName,
                    "assitMgr": this.queryBean.newAssitMgr,
                    "assitMgrName": this.queryBean.newAssitMgrName,
                    "dividerate": this.queryBean.newDividerate
                });
                this.newCooperatorBtn();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: "请不要重复保存！" }];
                this.newCooperatorReset();
            }
        }
    }

