import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { stockSystemMarketingQueryBean } from "./bean/stockSystemMarketingQueryBean";
import { CusViewHttpService } from "app/pages/tzb/custom/http/custom-view/custom-view.http.service";
import { Message } from "primeng/primeng";
import { Commfunc } from "../../../../../../../@uisftech/common/service/commonFun";
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
    selector: 'stockSystemMarketingQuery',
    templateUrl: './stockSystemMarketingQuery.component.html',
    styleUrls: ['./stockSystemMarketingQuery.component.scss'],
    providers: [Commfunc]
})
export class StockSystemMarketingQueryComponent implements OnInit {
    msgs: Message[];
    queryBean: stockSystemMarketingQueryBean = new stockSystemMarketingQueryBean();
    detailsDisplay: boolean = false;
    windowShareholdingNo: string;//窗口内的股份制编号
    windowFounder: string;//窗口内的创建人
    windowFoundDate: string;//窗口内的创建日期
    windowInstitution: string;//窗口内的所属机构id
    windowInstitutionName: string;//窗口内的所属机构名称
    tabMesg: any = tableMessage;
    tableValueList: any = [];
    total: string;//总页数
    first: number = 0;
    pageNum: number;//当前页数
    pageSize: number;//每页条数
    //控制窗口内显示的信息
    userDisplay: boolean = false;//分成账户信息
    userTableValueList: any = [];//分成账户信息 表格内容
    userFirst: number = 0;
    userPageNum: number = 1;
    userPageSize: number = 10;
    userTotal: string;//总页数
    cooperatorDisplay: boolean = false;//分成协储员信息 
    cooperatorTableValueList: any = [];//分成协储员信息 表格内容
    cooperatorFirst: number = 0;
    cooperatorPageNum: number = 1;
    cooperatorPageSize: number = 10;
    cooperatorTotal: string;//总页数

    workflowDisplay: boolean = false;//工作流历史
    workflowTableValueList: any = [];//分成协储员信息 表格内容
    workflowFirst: number = 0;
    workflowPageNum: number = 1;
    workflowPageSize: number = 10;
    workflowTotal: string;//总页数

    orgTreeDisplay: boolean = false;
    openInstitutionName: string;
    ownerOrgInValue: any;
    approvedStockStatus: any = [];
    code: any;
    
    constructor(
        public router: Router,
        public httpService: CusViewHttpService,
        public commfunc: Commfunc
    ) {
        this.codeValues();
        this.approvedStockStatus = this.code['ApprovedStockStatus'];
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        this.userDisplay = true;
        this.loadQuery();
    }

    loadQuery() {
        this.pageNum = 1;
        this.pageSize = 10;
        this.first = 0;
        let param = {
            operationType: 1,//操作类型
            seqno: this.queryBean.shareholdingNo,//股份制申请编号
            accountNo: this.queryBean.user,//账号
            custCode: this.queryBean.customerNo,//客户号
            custName: this.queryBean.customerName,//客户名称
            openBranch: this.queryBean.openInstitution,//开户机构
            pageNum: this.pageNum,//当前页数
            pageSize: this.pageSize//每页条数
        };
        this.httpService.queryJointStocksApp(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableValueList = data.stocksAppList;
                this.total = data.totalNum;
                this.pageNum = data.pageNum;
                this.pageSize = data.pageSize;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    queryFirst() {
        this.pageNum = 1;
        this.pageSize = 10;
        this.first = 0;
        this.query();
    }

    // 查询
    query() {
        let param = {};
        param = {
            operationType: 1,//操作类型
            seqno: this.queryBean.shareholdingNo,//股份制申请编号
            accountNo: this.queryBean.user,//账号
            custCode: this.queryBean.customerNo,//客户号
            custName: this.queryBean.customerName,//客户名称
            openBranch: this.queryBean.openInstitution,//开户机构
            pageNum: this.pageNum,//当前页数
            pageSize: this.pageSize//每页条数
        };
        this.httpService.queryJointStocksApp(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableValueList = data.stocksAppList;
                this.total = data.totalNum;
                this.pageNum = data.pageNum;
                this.pageSize = data.pageSize;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    // 重置
    reset() {
        this.queryBean.shareholdingNo = undefined;
        this.queryBean.user = undefined;
        this.queryBean.customerNo = undefined;
        this.queryBean.customerName = undefined;
        this.queryBean.openInstitution = undefined;
        this.openInstitutionName = undefined;
    }

    //翻页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows * 1;
        this.pageNum = event.page + 1;
        this.first = (this.pageNum - 1) * this.pageSize;
        this.query();
    }

    //点击编号弹出详情窗口
    windowData: any;
    toDetails(col) {
        this.windowShareholdingNo = col.seqno;
        this.windowFounder = col.creater;
        this.windowFoundDate = col.createDate;
        this.windowInstitution = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;//创建人机构Id
        this.windowInstitutionName = JSON.parse(this.commfunc.getSessionDataCH('chName')).orgName;//创建人机构
        this.userFirst = 0;
        this.userPageNum = 1;
        this.userPageSize = 10;
        this.cooperatorFirst = 0;
        this.cooperatorPageNum = 1;
        this.cooperatorPageSize = 10;
        this.workflowFirst = 0;
        this.workflowPageNum = 1;
        this.workflowPageSize = 10;
        this.windowData = col;
        this.queryUser();
        this.detailsDisplay = true;
    }

    queryUser() {
        let param = {};
        param = {
            operationType: 0,//操作类型
            seqno: this.windowData.seqno,//股份制申请编号
            pageNum: this.userPageNum,
            pageSize: this.userPageSize
        };
        this.httpService.queryJointStocksInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.userTableValueList = data.accountList;
                this.userTotal = data.totalNum;
                this.userPageNum = data.pageNum;
                this.userPageSize = data.pageSize;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    //分成账户信息 翻页
    userPaginate(event) {
        this.userPageSize = event.rows * 1;
        this.userPageNum = event.page + 1;
        this.userFirst = (this.userPageNum - 1) * this.userPageSize;
        this.queryUser();
    }

    //查询分成协储员信息
    queryCooperator() {
        let param = {};
        param = {
            operationType: 1,//操作类型
            seqno: this.windowData.seqno,//股份制申请编号
            pageNum: this.cooperatorPageNum,
            pageSize: this.cooperatorPageSize
        };
        this.httpService.queryJointStocksInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.cooperatorTableValueList = data.assitList;
                this.cooperatorTotal = data.totalNum;
                this.cooperatorPageNum = data.pageNum;
                this.cooperatorPageSize = data.pageSize;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    //分成协储员信息 翻页
    cooperatorPaginate(event) {
        this.cooperatorPageSize = event.rows * 1;
        this.cooperatorPageNum = event.page + 1;
        this.cooperatorFirst = (this.cooperatorPageNum - 1) * this.cooperatorPageSize;
        this.queryCooperator();
    }

    //查询工作流历史
    queryWorkflow() {
        let param = {};
        param = {
            operationType: 2,//操作类型
            seqno: this.windowData.seqno,//股份制申请编号
            pageNum: this.workflowPageNum,
            pageSize: this.workflowPageSize
        };
        this.httpService.queryJointStocksInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.workflowTableValueList = data.workflowList;
                this.workflowTotal = data.totalNum;
                this.workflowPageNum = data.pageNum;
                this.workflowPageSize = data.pageSize;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    //工作流历史 翻页
    workflowPaginate(event) {
        this.workflowPageSize = event.rows * 1;
        this.workflowPageNum = event.page + 1;
        this.workflowFirst = (this.cooperatorPageNum - 1) * this.cooperatorPageSize;
        this.queryWorkflow();
    }

    //用与切换
    toQH(i) {
        if (i == 1) {
            document.getElementById("zhxx").className = 'titBgColor active';
            document.getElementById("cooperator").className = 'titBgColor';
            document.getElementById("workflowls").className = 'titBgColor';
            this.queryUser();
            this.userDisplay = true;
            this.cooperatorDisplay = false;
            this.workflowDisplay = false;
        }
        if (i == 2) {
            document.getElementById("zhxx").className = 'titBgColor';
            document.getElementById("cooperator").className = 'titBgColor active';
            document.getElementById("workflowls").className = 'titBgColor';
            this.queryCooperator();
            this.userDisplay = false;
            this.cooperatorDisplay = true;
            this.workflowDisplay = false;
        }
        if (i == 3) {
            document.getElementById("zhxx").className = 'titBgColor';
            document.getElementById("cooperator").className = 'titBgColor';
            document.getElementById("workflowls").className = 'titBgColor active';
            this.queryWorkflow();
            this.userDisplay = false;
            this.cooperatorDisplay = false;
            this.workflowDisplay = true;
        }
    }

    //选择机构
    chooseOrg() {
        this.orgTreeDisplay = !this.orgTreeDisplay;
        this.ownerOrgInValue = 'adjustOrg'
    }

    //机构树回调
    treeCall(param) {     
        this.orgTreeDisplay = false;
        this.openInstitutionName = param.orgName;
        this.queryBean.openInstitution = param.orgId;
    }
    //点击X，关闭模态框
    onHideClose() {
        this.orgTreeDisplay = false;
    }
}