import { Component, OnInit } from "@angular/core";
import { CusOperationHttpService } from "../../../../http/custom-operation/custom-operation.http.service";
import { Commfunc } from "../../../../../../../@uisftech/common/service/commonFun";
import { FormGroup } from "@angular/forms";
import { QueryInterestRateFloatingBean } from "./bean/queryInterestRateFloating.bean";
import { CALENDAR_CN, tableMessage } from "../../../../../../../@uisftech/common/constant/config";
import { Router } from "@angular/router";

@Component({
    selector: 'upwardFluctuation',
    templateUrl: './upwardFluctuation.component.html',
    styleUrls: ['./upwardFluctuation.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class UpwardFluctuationComponent implements OnInit {
    userform: FormGroup;
    queryFloatingBean: QueryInterestRateFloatingBean = new QueryInterestRateFloatingBean();
    temData: Date = new Date();
    StartDt: any;//查询申请的开始时间
    EndDt: any;//查询申请的结束时间
    ch: any = CALENDAR_CN;
    orgName: any;//经办机构
    popName: any;//经办人
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    tableValueList: any = [{//表格内容
        "productId":123,
        "productId1":223,
        "productId2":323,
        "productId3":423,
        "productId4":523,
        "productId5":623,
        "productId6":723,
        "productId7":823,
        "productId8":923,
        "productId9":1023,
        "productId0":1123
    }];
    depositDisplay: boolean = false;//表格详情按钮开关
    depositValue: any = [];//选择的详情数据
    total: string = "1";//总页数
    first: number = 0;//分页控制

    constructor(
        public router: Router,
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc
    ) {
        
    }

    ngOnInit(){
        
    }

    toCurrentDepositQuery() {
        this.router.navigate(["pages/tzb/custom/work-rask/currentDeposit/currentDepositQuery"])
    }

    //查询时间
    showData() {
        this.temData = this.StartDt;
    }

    //查询
    query(){
        this.first = 0;
        this.queryProductAssocDefault();
    }

    //重置
    reset(){

    }

    // 点击获取详情
    depositDetails(item){
        this.depositDisplay = true;
        this.depositValue = item;
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.queryFloatingBean.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.queryFloatingBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.queryFloatingBean.pageSize;
        //调用查询的方法
        this.queryProductAssocDefault();
        this.queryFloatingBean.pageNum = 1;
    }

    // 默认查询
    queryProductAssocDefault() {
        this.tableValueList = [{//表格内容
            "productId":123,
            "productId1":223,
            "productId2":323,
            "productId3":423,
            "productId4":523,
            "productId5":623,
            "productId6":723,
            "productId7":823,
            "productId8":923,
            "productId9":1023,
            "productId0":1123
        }];
        this.total = "1";//总页数
        // this.prcBean.flag = this.relationValue.value[0];
        // this.productPlatformHttpService.queryProductAssocDefault(this.prcBean).subscribe(data => {
        // if (data.returnCode.code == "success") {
        //     this.cars_2 = data.productAssocList
        //     this.total = data.total;
        // }
        // else {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        // }
        // }, error => {
        // this.msgs = [];
        // this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        // })
    }

    //查询始终第一页
    querySecle() {
        this.queryFloatingBean.pageSize = 10;
        this.queryFloatingBean.pageNum = 1;
        this.first = 0;
        this.query();
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }
    
}
