    // 选人回调
    outDisplayCall(event) {
        this.dealPerShow = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
            this.specifyNextNode = item.outFlowId;
            this.nodeType = item.nodeType;
        });
        // 非会签节点
        if (this.nodeType != 'sign') {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            commonHeader.extParam['specifyNextOpers'] = ss;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.subAudit();
    }

    // 提交
    subAudit() {
        let param = {};
        param = {
            isSkip: this.specifyNextNode,//是否跳过开户主管
            seqno: this.shareholdingNo,//股份制编号
            workFlowType: this.s5,
            postOrg: this.s6,
        };
        this.httpService.appJointStocksCommit(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交审批成功！' }];
                this.doEmptyAll();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }

