    // 开始、结束日期判断
    showTime(param1, param2) {
        if (param1 == 'plan') {
            if (this.startDate && this.divideLoseDate) {
                if (this.startDate > this.divideLoseDate) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.startDate = null;
                        } else {
                            this.divideLoseDate = null;
                        }
                    })
                }
            }
        }
    }

