import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';


//页面
import { CreditIntergrationComponent } from './credit-intergration/credit-intergration.component';//信贷综合岗界面
import { CreditApplyComponent } from './credit-intergration/credit-apply/credit-apply.component';//信贷业务申请登记
import { CustomerRelatinshipComponent } from './credit-intergration/customer-relationship/customer-relationship.component';//客户关联关系
import { CusRelationApplyComponent } from './credit-intergration/customer-relationship/cusRelationApply/cusRelationApply.component';
import {CusRelationUpdateComponent} from './credit-intergration/customer-relationship/cusRelationUpdate/cusRelationUpdate.component';
import {CusRelationQueryComponent} from './credit-intergration/customer-relationship/cusRelationQuery/cusRelationQuery.component';
import {CusRelationUpdateQueryComponent} from './credit-intergration/customer-relationship/cusRelationUpdateQuery/cusRelationUpdateQuery.component';

import {StockSystemMarketingComponent} from './credit-intergration/stockSystemMarketing/stockSystemMarketing.component';
import {StockSystemMarketingQueryComponent} from './credit-intergration/stockSystemMarketing/stockSystemMarketingQuery/stockSystemMarketingQuery.component';
import {StockSystemMarketingApplyComponent} from './credit-intergration/stockSystemMarketing/stockSystemMarketingApply/stockSystemMarketingApply.component';
import {StockSystemMarketingUnauditedQueryComponent} from './credit-intergration/stockSystemMarketing/stockSystemMarketingUnauditedQuery/stockSystemMarketingUnauditedQuery.component';
import {StockSystemMarketingUserQueryComponent} from './credit-intergration/stockSystemMarketing/stockSystemMarketingUserQuery/stockSystemMarketingUserQuery.component';

import {UpwardFluctuationComponent} from './credit-intergration/currentDeposit/upwardFluctuation/upwardFluctuation.component';
import {CurrentDepositComponent} from './credit-intergration/currentDeposit/currentDeposit.component';
import {CurrentDepositApplyComponent} from './credit-intergration/currentDeposit/currentDepositApply/currentDepositApply.component';
import {CurrentDepositQueryComponent} from './credit-intergration/currentDeposit/currentDepositQuery/currentDepositQuery.component';
import { stockSystemMarketingExamComponent } from 'app/pages/tzb/custom/work-rask/credit-intergration/stockSystemMarketing/stockSystemMarketingExam/stockSystemMarketingExam.component';
import { StockSystemMarketingChangeHistoryComponent } from 'app/pages/tzb/custom/work-rask/credit-intergration/stockSystemMarketing/stockSystemMarketingChangeHistory/stockSystemMarketingChangeHistory.component';

export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'credit-intergration',
                component: CreditIntergrationComponent
            },
            { path: 'credit-apply', component: CreditApplyComponent },
            {
                path: 'customer-relationship',
                component: CustomerRelatinshipComponent,
                children: [
                    {
                        path: 'cusRelationApply',
                        component: CusRelationApplyComponent
                    },
                    {
                        path:'cusRelationUpdate',
                        component:CusRelationUpdateComponent
                    },
                    {
                        path:'cusRelationQuery',
                        component:CusRelationQueryComponent
                    },
                    {
                        path:'cusRelationUpdateQuery',
                        component:CusRelationUpdateQueryComponent
                    }
                ]
            },
            {
                path:'customIntroduce',
                loadChildren:'./credit-intergration/customIntroduce/customIntroduce.module#CustomIntroduceModule'
            },
            // { path: 'stockSystemMarketing', component: StockSystemMarketingComponent },
            {
                path: 'stockSystemMarketingExam',
                component: stockSystemMarketingExamComponent
            },
            {
                path: 'stockSystemMarketing', 
                component: StockSystemMarketingComponent,
                children:[
                    { path: '', redirectTo: 'stockSystemMarketingApply', pathMatch: 'full' },
                    {
                        path:'stockSystemMarketingApply',
                        component:StockSystemMarketingApplyComponent
                    },
                    {
                        path:'stockSystemMarketingUnauditedQuery',
                        component:StockSystemMarketingUnauditedQueryComponent
                    },
                    // {
                    //     path:'stockSystemMarketingQuery',
                    //     component:StockSystemMarketingQueryComponent
                    // },
                    {
                        path:'stockSystemMarketingUserQuery',
                        component:StockSystemMarketingUserQueryComponent
                    },
                    {
                        path:'stockSystemMarketingChangeHistory',
                        component:StockSystemMarketingChangeHistoryComponent
                    },
                ]
            },
            {
                path: 'stockSystemMarketings', 
                component: StockSystemMarketingComponent,
                children:[
                    { path: '', redirectTo: 'stockSystemMarketingApply', pathMatch: 'full' },
                    {
                        path:'stockSystemMarketingApply',
                        component:StockSystemMarketingApplyComponent
                    },
                    {
                        path:'stockSystemMarketingUnauditedQuery',
                        component:StockSystemMarketingUnauditedQueryComponent
                    },
                    // {
                    //     path:'stockSystemMarketingQuery',
                    //     component:StockSystemMarketingQueryComponent
                    // },
                    {
                        path:'stockSystemMarketingUserQuery',
                        component:StockSystemMarketingUserQueryComponent
                    },
                    {
                        path:'stockSystemMarketingChangeHistory',
                        component:StockSystemMarketingChangeHistoryComponent
                    },
                ]
            },
            {
                path:'currentDeposit',
                children:[
                    {
                        path:'currentDepositQuery',
                        component:CurrentDepositQueryComponent
                    },
                    {
                        path:'currentDepositApply',
                        component:CurrentDepositApplyComponent
                    },
                    {
                        path:'upwardFluctuation',
                        component:UpwardFluctuationComponent
                    }
                ]
            },
            
        ]
    }

]

export const WorkRasktRouting: ModuleWithProviders = RouterModule.forChild(routes);
