import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
@Component({
    selector: 'cusRelationUpdateQuery',
    templateUrl: './cusRelationUpdateQuery.component.html',
    styleUrls: ['./cusRelationUpdateQuery.component.scss']
})
export class CusRelationUpdateQueryComponent implements OnInit {
    addHeader = '选择字段';
    addPage: boolean = false;
    detailHeader = '详情';
    detailPage: boolean = false;
    isAll: boolean = false;
    tableData: any = [
        {
            label: '客户号',
            value: 'a', //对应字段名
            isCheck: true,
            num: '1'
        },
        {
            label: '客户名字',
            value: 'b',
            isCheck: true,
            num: '2'
        },
        {
            label: '关联客户号',
            value: 'c',
            isCheck: true,
            num: '3'
        },
        {
            label: '关联客户',
            value: 'd',
            isCheck: true,
            num: '4'
        },
    ];
    arrList: any = [
        {
            label: '客户号',
            value: 'a',//对应字段名
            isCheck: true,
            num: '1'
        },
        {
            label: '客户名字',
            value: 'b',
            isCheck: true,
            num: '2'
        },
        {
            label: '关联客户号',
            value: 'c',
            isCheck: true,
            num: '3'
        },
        {
            label: '关联客户',
            value: 'd',
            isCheck: true,
            num: '4'
        },
        {
            label: '经办机构',
            value: 'e',
            num: '5'
        },
        {
            label: '关联关系',
            value: 'f',//对应字段名
            num: '6'
        },
        {
            label: '修改人',
            value: 'g',
            num: '7'
        },
        {
            label: '修改时间',
            value: 'h',//对应字段名
            num: '8'
        },
        {
            label: '是否可冲减积数',
            value: 'i',
            num: '9'
        },
        {
            label: '积数冲减控制方式',
            value: 'j',
            num: '10'
        },
        {
            label: '状态',
            value: 'k',
            num: '11'
        },
        {
            label: '关系描述',
            value: 'l',
            num: '12'
        },
        {
            label: '备注',
            value: 'm',//对应字段名
            num: '13'
        },
        {
            label: '审批状态',
            value: 'n',
            num: '14'
        },
        {
            label: '当前审批阶段',
            value: 'o',//对应字段名
            num: '15'
        },
        {
            label: '上一审批阶段',
            value: 'p',
            num: '16'
        },
        {
            label: '审批开始时间',
            value: 'q',
            num: '17'
        },
        {
            label: '审批结束时间',
            value: 'r',
            num: '18'
        },
        {
            label: '审批意见',
            value: 's',
            num: '19'
        },
        {
            label: '申请用户',
            value: 't',
            num: '20'
        }
    ];
    data = [
        {
            a: '1',
            b: '2',
            c: '3',
            d: '4'
        }
    ];
    constructor(
        public router: Router,
    ) { }

    ngOnInit() {
    }
    toCusRelationApplyQry() {
        this.router.navigate(["pages/tzb/custom/work-rask/customer-relationship/cusRelationQuery"])
    }
    chooseBtn() {
        this.addPage = true;
    }
    doSelect(data) {
        let sb = true;
        if (data.isCheck) {
            this.tableData.push(data);
            this.arrList.forEach(item => {
                sb = item.isCheck && sb
            });
        } else {
            this.tableData.forEach((item, i) => {
                if (item.value == data.value) {
                    this.tableData.splice(i, 1);
                    return;
                }
            })
            sb = false;
        }
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
        this.isAll = sb;
    }
    checkAll() {
        let isTrue = this.isAll;
        this.tableData = [];
        this.arrList.forEach(item => {
            item['isCheck'] = isTrue;
            if (isTrue) {
                this.tableData.push(item);
            }
        });
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
    }
    toDetail(item) {
        this.detailPage = true;
    }
    save() {
        this.addPage = false;
    }
}
