import { Component, OnInit } from "@angular/core";
import { CusOperationHttpService } from "../../../../http/custom-operation/custom-operation.http.service";
import { Commfunc } from "../../../../../../../@uisftech/common/service/commonFun";
import { FormGroup, FormBuilder } from "@angular/forms";
import { CurrentDepositApplyBean } from "./bean/currentDepositApply.bean";
import { TZB_CUS_UPLOAD } from "../../../../../../../@uisftech/common/service/http.constant";
import { Router } from "@angular/router";

@Component({
    selector: 'currentDepositApply',
    templateUrl: './currentDepositApply.component.html',
    styleUrls: ['./currentDepositApply.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class CurrentDepositApplyComponent implements OnInit {
    code: any;
    userform: FormGroup;
    productTypeCurrent: any;//产品类型List
    depositApplyBean: CurrentDepositApplyBean = new CurrentDepositApplyBean();
    items: any = [
        { label: '请选择', value: '' }, { label: '是', value: '1' }, { label: '否', value: '2' }
    ];//是否大客户费用List
    bigCustomer: any;//大客户费用
    uploadedFiles: any[] = [];
    UPLOAD: string = TZB_CUS_UPLOAD;
    
    constructor(
        public router: Router,
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        
    }

    ngOnInit(){
        this.codeValues();
        this.productTypeCurrent = this.code['ProductTypeCurrent'];
    }

    myUploader(event) {
        if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
            this.uploadedFiles = JSON.parse(event.xhr.response);
        }
        this.depositApplyBean.fileName = this.uploadedFiles[0].fileName;
        this.depositApplyBean.fileUrl = this.uploadedFiles[0].fileUrl;
    }

    // 提交
    query(){

    }

    // 重置
    reset(){
        
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}
