import { Component, Input, OnInit } from '@angular/core';
import { Commfunc } from '../../../../../../../../@uisftech/common/service/commonFun';
import { ProductManageHttpService } from '../../../../../../product-manage/http/product-manage.http.service';


@Component({
    selector: 'detailsWindow',
    templateUrl: './detailsWindow.component.html',
    styleUrls: ['./detailsWindow.component.scss'],
    providers: [ProductManageHttpService, Commfunc]
})
/**
 * 机构对应关系
 */
export class DetailsWindowComponent implements OnInit {
    // @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;
    productId:String;
    tableValueList: any = [{//表格内容
        "productId":1,
        "productId1":2,
        "productId2":3,
        "productId3":4,
        "productId4":5,
        "productId5":6,
        "productId6":7,
        "productId7":8
    }];
    pageNum: number;//当前页
    pageSize: number = 10;//每页多少条
    total: number = 1;//总页数
    first: number = 0;//分页控制

    constructor(
        public productPlatformHttpService: ProductManageHttpService,
        public commfunc: Commfunc
    ) {
        
    }

    ngOnInit(){
        this.productId = this.inValue.productId;
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        //调用查询的方法
        this.queryProductAssocDefault();
        this.pageNum = 1;
    }

    // 默认查询
    queryProductAssocDefault() {
        this.tableValueList = [{//表格内容
            "productId":1,
            'productId1':2,
            'productId2':3,
            'productId3':4,
            'productId4':5,
            'productId5':6,
            'productId6':7,
            'productId7':8
        }];
        this.total = 1;//总页数
    }
}