import { Component, OnInit } from '@angular/core';
import { WorkTaskBean } from './bean/workrask.bean';
import { TaskUserRelationDelBean } from './bean/taskUserRelationDel.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { ConfirmDialogModule, ConfirmationService, Message, SelectItem } from 'primeng/primeng';
import { Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CommonSerachBean } from './bean/common-serach.bean';
@Component({
  selector: 'app-credit-intergration',
  templateUrl: './credit-intergration.component.html',
  styleUrls: ['./credit-intergration.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class CreditIntergrationComponent implements OnInit {
  serachList: any = [];
  leftTitList: any = [];
  Header: string = '常用功能维护';
  tep: number;
  queryLoanList: any;
  loanTypeId: string;
  loanquerypage: boolean = false;
  msgs: Message[] = [];
  checkAddList: any = [];
  addArr: any = [];
  addList: any;
  checkList: any = [];
  isChecked: boolean;
  CheckBox: boolean;
  deteleList: any = [];
  listDel: any;
  items = [];
  serachshow: boolean = false;
  comshow: boolean = true;
  loanshow: boolean = false;
  display: boolean = true;
  preserveshow: boolean = false;
  addshow: boolean = true;
  deleteshow: boolean = false;
  applyfor: boolean = true;
  querypage: boolean = false;
  creatPage: boolean = false;
  customcreate: boolean = true;
  bulkImport: boolean = false;
  customImport: boolean = false;
  customHistory: boolean = false;
  repealPage: boolean = false;

  repealHistory: boolean = false;
  customQry: boolean = false;
  relationPrePage: boolean = false;

  num: number;
  item: number;
  searchNum: number;
  //总页数
  total: string = "200";
  //每页多少条
  pageSize: string = '10';
  taskUserRelationDelBean: TaskUserRelationDelBean = new TaskUserRelationDelBean();
  workTaskBean: WorkTaskBean = new WorkTaskBean();
  commonSerachBean: CommonSerachBean = new CommonSerachBean();
  titleList: any;
  taskTypeParentId: any;
  tasklistChildren: any;
  itemList: any;
  user: any;
  orgId: string = '';
  userId: string = '';
  constructor(
    public httpService: CusOperationHttpService,
    public router: Router,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (this.user.orgId) {
      this.orgId = this.user.orgId;
    }
    if (this.user.userId) {
      this.userId = this.user.userId;
    }
    let item = {
      taskTypeKey: 'taskType',
    }
    this.httpService.taskTypeParentQry(item).subscribe(data => {
      this.titleList = data.taskTypeList;
      this.leftTitList = [];
      this.leftTitList = [{ label: '请选择', value: '' }, { label: '常用功能', value: '00' }];
      for (let item of this.titleList) {
        this.leftTitList.push({ label: item.taskTypeName, value: item.taskTypeId })
      }
    });
    this.querymore();
    this.allQuery();

  }

  ngOnInit() {
    this.num = 1111;
    this.applyfor = true;
  }
  //常用功能全部查询
  allQuery() {
    this.httpService.allTaskQry({}).subscribe(data => {
      this.addList = data.taskByTypeList || [];
      this.addList.forEach((item) => {
        for (let temp of item.taskList) {
          if (temp.checked == '0') {
            temp.CheckBox = true;
          }
        }
      });
    })
  }
  //常用功能查询
  querymore() {
    this.httpService.taskUserRelationQry({}).subscribe(data => {
      // debugger
      this.items = [];
      this.items = data.taskCategoryList;
    })
  }
  //常用功能
  common(i) {
    this.num = i;
    this.comshow = true;
    this.loanshow = false;
    this.applyfor = true;
    this.display = true;
    this.preserveshow = false;
    this.querypage = false;
    this.item = 3;
    this.creatPage = false;
    this.customHistory = false;
    this.repealPage = false;

    this.repealHistory = false;
    this.customQry = false;
    this.relationPrePage = false;
    this.serachshow = false;

  }
  //搜索
  search() {
    this.commonSerachBean.taskType = '';
    this.httpService.taskQryByType(this.commonSerachBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.serachList = data.taskByTypeList;
        this.serachshow = true;
        this.comshow = false;
        this.loanshow = false;
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
    this.searchNum = 1;
  }
  //搜索申请
  searchApply() { }
  //搜索查询
  searchQuery() {
    this.commonSerachBean.taskType = '04';
    this.httpService.taskQryByType(this.commonSerachBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.serachList = data.taskByTypeList;
        this.serachshow = true;
        this.comshow = false;
        this.loanshow = false;
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
    this.searchNum = 2;
  }
  //贷款
  loans(i) {
    this.item = 888888;
    this.loanTypeId = i;
    let param = {
      taskTypeParentId: i,
    };
    this.httpService.taskQryByType(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tasklistChildren = data.taskByTypeList;
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
    this.num = i;
    this.comshow = false;
    this.loanshow = true;
    this.loanquerypage = false;
    this.querypage = false;
    this.applyfor = true;
    this.creatPage = false;
    this.customHistory = false;
    this.repealPage = false;
    this.repealHistory = false;
    this.customQry = false;
    this.relationPrePage = false;
    this.serachshow = false;

  }
  add() {
    this.addshow = true;
    this.deleteshow = false;
  }
  delete() {
    this.addshow = false;
    this.deleteshow = true;
  }
  preserve() {
    this.display = false;
    this.preserveshow = true;
    this.addshow = true;
    this.deleteshow = false;
    this.allQuery();
  }
  comeback() {
    this.display = true;
    this.comshow = true;
    this.applyfor = true;
    this.preserveshow = false;
  }
  //常用维护新增
  checkAdd(item) {
    if (item.CheckBox) {
      this.checkAddList.push(item)
    } else {
      this.checkAddList.forEach((tep, i) => {
        if (item.taskCategoryId == tep.taskCategoryId) {
          this.checkAddList.splice(i, 1)
        }
      });
    }
  }
  //常用功能维护新增
  preserveAdd(param) {
    let arrlist = [];
    // this.checkAddList.forEach(element => {
    //   arrlist.push(element.taskCategoryId)
    // });
    // this.taskUserRelationDelBean.taskCategoryList = arrlist;
    param.forEach(item => {
      for (let temp of item.taskList) {
        if (temp.CheckBox) {
          arrlist.push(temp.taskCategoryId)
        }
      }
    });
    this.taskUserRelationDelBean.taskCategoryList = arrlist;
    this.httpService.taskUserRelationAdd(this.taskUserRelationDelBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功' });
        this.checkAddList = [];
        this.querymore();
        this.num = 1111;
        this.applyfor = true;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    });
  }
  //常用功能维护新增重置
  chongzhiAdd() {
    this.addList.forEach((item) => {
      for (let temp of item.taskList) {
        temp.CheckBox = false;
      }
    });
  }
  addHidden() {
    this.display = true;
    this.comshow = true;
    this.applyfor = true;
    this.preserveshow = false;
  }
  //常用功能维护删除
  preserveDelete() {
    this.confirmationService.confirm({
      message: '是否确认删除?',
      header: '删除',
      icon: 'fa fa-trash',
      accept: () => {
        let arrlist = [];
        this.checkList.forEach(element => {
          arrlist.push(element.value)
        });
        this.taskUserRelationDelBean.taskCategoryList = arrlist;
        this.httpService.taskUserRelationDel(this.taskUserRelationDelBean).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs = [{ severity: 'info', summary: '确认删除', detail: '删除成功' }];
            this.checkList = [];
            this.querymore();
            this.allQuery();
          } else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '确认删除', detail: data.returnCode.message }];
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })

      },
      reject: () => {
        this.msgs = [{ severity: 'info', summary: '取消删除', detail: '删除已取消' }];
      }
    });
  }
  //常用功能维护删除重置
  chongzhiDel() {
    this.deteleList.forEach(item => {
      item.isChecked = false;
    });
  }
  //常用维护删除
  checkDelete(param) {
    if (param.isChecked == true) {
      this.checkList.push(param)
    } else if (param.isChecked == false) {
      this.checkList.forEach((item, i) => {
        if (param.value == item.value) {
          this.checkList.splice(i, 1)
        }
      });
    }
  }
  apply(i) {
    this.applyfor = true;
    this.querypage = false;
    this.loanquerypage = false;
    this.item = i;
    this.deleteshow = false;
    this.addshow = true;
  }
  query(i) {
    this.loanquerypage = true;
    this.applyfor = false;
    let param = {
      taskTypeParentId: i,
      taskType: '04'
    }
    this.httpService.taskQryByType(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.queryLoanList = data.taskByTypeList;
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '确认删除', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    });
    this.item = i;
  }
  //申请创建 常用功能跳转外系统
  openAdd(item) {

    this.workTaskBean.userId = this.userId;
    this.workTaskBean.sysCode = '122';
    this.workTaskBean.casServerLoginUrl = 'http://10.1.39.140:8011/searchPaymentTool';
    this.workTaskBean.serverName = 'ywsn';
    this.workTaskBean.validateType = '0';
    this.workTaskBean.extraInfo = 'ywsn';
    this.workTaskBean.encryptType = 'HS256';
    if (item.subSysId == 'LOAN') {
      let timestamp = new Date().getTime();
      let target = "custLinkForm" + timestamp;
      // this.commfunc.createBlank(target);
      this.httpService.tokenApply(this.workTaskBean).subscribe(data => {
        if (data.returnCode.code == 'success') {
          let tokens = data.tokenCode;
          let url = item.url + '&token=' + tokens + '&suid=' + this.userId;
          let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
          let closeOnLoseFocus = 'closeOnLoseFocus';
          this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
        }
      });
    } else if (item.subSysId == 'CRM') {
      let timestamp = new Date().getTime();
      let target = "custLinkForm" + timestamp;
      // this.commfunc.createBlank(target);
      this.httpService.tokenApply(this.workTaskBean).subscribe(data => {
        if (data.returnCode.code == 'success') {
          let tokens = data.tokenCode;
          let url = item.url + '&token_id=' + tokens + '&user_id=' + this.userId;
          let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
          let closeOnLoseFocus = 'closeOnLoseFocus';
          this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
        }
      });
    } else {
      if (item.taskCategoryId == '388000') {//卡要素调整查询，跳转内系统
        this.router.navigate(['/pages/tzb/custom/credit-ccm/cardElementUpateList']);
      } else {
        this.router.navigate([item.url, { taskCategoryId: item.taskCategoryId }], { preserveFragment: true })

      }
    }
  }
  creat(i) {
    this.num = i;
    this.creatPage = true;
    this.comshow = false;
    this.loanshow = false;
    this.repealPage = false;
    this.customHistory = false;
    this.repealHistory = false;
    this.customQry = false;
    this.relationPrePage = false;

  }
  cusCreate() {
    this.customcreate = true;
    this.bulkImport = false;
    this.customImport = false;
    this.customHistory = false;
  }
  import1() {
    this.customcreate = false;
    this.bulkImport = true;
    this.customImport = false;
  }
  import2() {
    this.customcreate = false;
    this.bulkImport = false;
    this.customImport = true;
  }
  //申请历史
  history(i) {
    this.num = i;
    this.customHistory = true;
    this.creatPage = false;
    this.comshow = false;
    this.loanshow = false;

    this.repealPage = false;
    this.repealHistory = false;
    this.customQry = false;
    this.relationPrePage = false;

  }
  //撤销申请
  cusRepeal(i) {
    this.num = i;
    this.repealPage = true;
    this.comshow = false;
    this.loanshow = false;
    this.customHistory = false;
    this.creatPage = false;

    this.repealHistory = false;
    this.customQry = false;
    this.relationPrePage = false;

  }
  //撤销历史
  historyRepeal(i) {
    this.num = i;
    this.repealHistory = true;
    this.comshow = false;
    this.loanshow = false;
    this.customHistory = false;
    this.creatPage = false;
    this.repealPage = false;

    this.customQry = false;
    this.relationPrePage = false;

  }
  //查询客户介绍
  queryCustom(i) {
    this.num = i;
    this.customQry = true;
    this.repealHistory = false;
    this.comshow = false;
    this.loanshow = false;
    this.customHistory = false;
    this.creatPage = false;
    this.repealPage = false;
    this.relationPrePage = false;
  }
  //客户关系维护
  relationPre(i) {
    this.num = i;
    this.relationPrePage = true;
    this.customQry = false;
    this.repealHistory = false;
    this.comshow = false;
    this.loanshow = false;
    this.customHistory = false;
    this.creatPage = false;
    this.repealPage = false;
  }
  toCusRelationApply() {
    this.router.navigate(["pages/tzb/custom/work-rask/customer-relationship/cusRelationApply"])
  }
  toCusRelationUpdate() {
    this.router.navigate(["pages/tzb/custom/work-rask/customer-relationship/cusRelationUpdate"])
  }
  toCusRelationApplyQry() {
    this.router.navigate(["pages/tzb/custom/work-rask/customer-relationship/cusRelationQuery"])
  }
  toCusRelationUpdateQuery() {
    this.router.navigate(["pages/tzb/custom/work-rask/customer-relationship/cusRelationUpdateQuery"])
  }
  toCustomIntroduceApply() {
    this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApply"])
  }
  toCustomIntroduceRepeal() {
    this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceRepeal"])
  }
  toCustomIntroduceApplyQuery() {
    this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApplyQuery"])
  }
  toCustomIntroduceRepealQuery() {
    this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceRepealQuery"])
  }
  toCustomIntroduceRelationshipQuery() {
    this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceRelationshipQuery"])
  }
  toStockSystemMarketingQuery() {
    this.router.navigate(["pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingQuery"])
  }
  toCurrentDepositApply() {
    this.router.navigate(["pages/tzb/custom/work-rask/currentDeposit/currentDepositApply"])
  }
  toCurrentDepositQuery() {
    this.router.navigate(["pages/tzb/custom/work-rask/currentDeposit/currentDepositQuery"])
  }

}
