import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';


@Component({
    selector: 'stockSystemMarketing',
    //   template: `<router-outlet></router-outlet>`
    templateUrl: './stockSystemMarketing.component.html',
    styleUrls: ['./stockSystemMarketing.component.scss'],
    providers: [Commfunc]
})
export class StockSystemMarketingComponent implements OnInit {

    show: number = 1;
    isAdd: boolean = true;
    upParam: any;
    theRequest: any = [];

    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public route: ActivatedRoute,
    ) {}

    ngOnInit() {
        let url = window.location.href
        if (url.indexOf(";") != -1) {
            let str = url.substr(1);
            let strs = str.split(";");
            for (let i = 0; i < strs.length; i++) {
                this.theRequest[strs[i].split("=")[0]] = (strs[i].split("=")[1]);
            }
        }
        if (this.theRequest.flag == 'update') {
            this.isAdd = false;
            this.upParam = {
                flag:  decodeURI(this.theRequest.flag),
                seqno: decodeURI(this.theRequest.seqno),
            }
        } else {
            this.isAdd = true;
        }
        this.judgStatu();
    }
    apply(num) {
        this.show = num;
    }
    //判断当前路由 改变样式active
    judgStatu() {
        let stu_1 = this.commfunc.permissionCheck('SID00027_03_01'); // 股份制分成申请
        let stu_2 = this.commfunc.permissionCheck('SID00027_03_02'); // 股份制申请历史
        let stu_3 = this.commfunc.permissionCheck('SID00027_03_03'); // 账户分成信息

        let url2 = window.location.href
        if(url2.includes('/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingApply')){
            if(stu_1){
                this.show = 1;
                if(this.isAdd){
                    this.router.navigate(['/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingApply'])
                }else{
                    this.router.navigate(['/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingApply',this.upParam])
                }
                return
            }else if(stu_2){
                this.show = 2;
                this.router.navigate(['/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingUnauditedQuery'])
                return
            }else if(stu_3){
                this.show = 2;
                this.router.navigate(['/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingUserQuery'])
                return
            }
        }else if (url2.includes('/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingUnauditedQuery')) {
            this.show = 2;
            this.router.navigate(['/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingUnauditedQuery'])
            return
		} else if (url2.includes('/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingUserQuery')) {
            this.show = 3;
            this.router.navigate(['/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingUserQuery'])
            return
		} else if (url2.includes('/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingChangeHistory')) {
            this.show = 4;
            this.router.navigate(['/pages/tzb/custom/work-rask/stockSystemMarketing/stockSystemMarketingChangeHistory'])
            return
		}
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

}
