import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'lodash';
@Component({
    selector: 'cusRelationQuery',
    templateUrl: './cusRelationQuery.component.html',
    styleUrls: ['./cusRelationQuery.component.scss']
})
export class CusRelationQueryComponent implements OnInit {
    addHeader = '选择字段';
    addPage: boolean = false;
    detailHeader = '详情';
    detailPage: boolean = false;
    isAll: boolean = false;
    tableData: any = [
        {
            label: '客户号',
            value: 'aa',//对应字段名
            isCheck: true,
            num: '1'
        },
        {
            label: '客户名字',
            value: 'bb',
            isCheck: true,
            num: '2'
        },
        {
            label: '关联客户号',
            value: 'cc',
            isCheck: true,
            num: '3'
        },
        {
            label: '关联客户',
            value: 'dd',
            isCheck: true,
            num: '4'
        },
    ];
    arrList: any = [
        {
            label: '客户号',
            value: 'aa',//对应字段名
            isCheck: true,
            num: '1'
        },
        {
            label: '客户名字',
            value: 'bb',
            isCheck: true,
            num: '2'
        },
        {
            label: '关联客户号',
            value: 'cc',
            isCheck: true,
            num: '3'
        },
        {
            label: '关联客户',
            value: 'dd',
            isCheck: true,
            num: '4'
        },
        {
            label: '经办机构',
            value: 'ee',
            num: '5'
        },
        {
            label: '关联关系',
            value: 'ff',//对应字段名
            num: '6'
        },
        {
            label: '是否可冲减积数',
            value: 'gg',
            num: '7'
        },
        {
            label: '积数冲减控制方式',
            value: 'hh',
            num: '8'
        },
        {
            label: '状态',
            value: 'ii',
            num: '9'
        },
        {
            label: '关系描述',
            value: 'jj',
            num: '10'
        },
        {
            label: '备注',
            value: 'kk',//对应字段名
            num: '11'
        },
        {
            label: '经办人',
            value: 'll',
            num: '12'
        },
        {
            label: '经办日期',
            value: 'mm',
            num: '13'
        },
        {
            label: '更新日期',
            value: 'nn',
            num: '14'
        },
        {
            label: '审批状态',
            value: 'oo',
            num: '15'
        },
        {
            label: '当前审批阶段',
            value: 'pp',//对应字段名
            num: '16'
        },
        {
            label: '上一审批阶段',
            value: 'qq',
            num: '17'
        },
        {
            label: '审批开始时间',
            value: 'rr',
            num: '18'
        },
        {
            label: '审批结束时间',
            value: 'ss',
            num: '19'
        },
        {
            label: '审批意见',
            value: 'tt',
            num: '20'
        },
        {
            label: '申请用户',
            value: 'uu',
            num: '21'
        }
    ];
    data = [
        {
            aa: 'zzz',
            bb: 'aaa',
            cc: 'qqq',
            dd: 'www',
            ee: 'sss',

        }
    ];
    constructor(
        public router: Router,
    ) { }

    ngOnInit() {
        this.arrList.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })

    }
    toCusRelationUpdateQuery() {
        this.router.navigate(["pages/tzb/custom/work-rask/customer-relationship/cusRelationUpdateQuery"])
    }
    chooseBtn() {
        this.addPage = true;
    }
    //单选
    doSelect(data) {
        let sb = true;
        if (data.isCheck) {
            this.tableData.push(data);
            this.arrList.forEach(item => {
                sb = item.isCheck && sb
            });
        } else {
            this.tableData.forEach((item, i) => {
                if (item.value == data.value) {
                    this.tableData.splice(i, 1);
                    return;
                }
            })
            sb = false;
        }
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
        this.isAll = sb;
    }
    //全选
    checkAll() {
        let isTrue = this.isAll;
        this.tableData = [];
        this.arrList.forEach(item => {
            item['isCheck'] = isTrue;
            if (isTrue) {
                this.tableData.push(item);
            }
        });
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
    }
    toDetail(item) {
        this.detailPage = true;
    }
    save() {
        this.addPage = false;
    }
}
